.searchBar{
    width: 25vw;
    height: 8vh;
    background-color: grey;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.input{
    width: 60%;
    height: 50%;
    border-radius: 0.3rem;
    border: none;
    padding: 0 0 0 1rem;
}

.boton{
    width: 6vw;
    height: 4vh;
    border-radius: 0.3rem;
    background-color: aqua;
    border: none;
    cursor: pointer;
}

.boton:hover{
    transform: scale(1.1);
}